import React, { useEffect, useState } from "react";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { useSession } from "../contexts/SessionProvider";

// Design matches main Header.js: centered logo, mobile overlay, hide-on-scroll, dark glass background.
// Main differences: routes point to /fib subtree; right-side shows auth status + logout when signed in.
function HeaderFIB() {
  const [isMobileOpen, setIsMobileOpen] = useState(false);
  const [showHeader, setShowHeader] = useState(true);
  const [lastScrollY, setLastScrollY] = useState(0);
  const { user, isAuthenticated, logout } = useSession();
  const nav = useNavigate();
  const loc = useLocation();

  // Hide-on-scroll behavior (mirrors main header)
  useEffect(() => {
    const controlHeaderVisibility = () => {
      if (typeof window !== "undefined") {
        const currentScrollY = window.scrollY;
        setShowHeader(currentScrollY < lastScrollY || currentScrollY === 0);
        setLastScrollY(currentScrollY);
      }
    };
    window.addEventListener("scroll", controlHeaderVisibility);
    return () => window.removeEventListener("scroll", controlHeaderVisibility);
  }, [lastScrollY]);

  // Lock body scroll when mobile menu open
  useEffect(() => {
    document.body.style.overflow = isMobileOpen ? "hidden" : "visible";
  }, [isMobileOpen]);

  const handleLogout = async () => {
    logout();
    if (!loc.pathname.startsWith("/fib")) {
      nav("/fib", { replace: true });
    }
     try { window.FIBNativeBridge?.sendMessage?.({ type: "EXIT" }); } catch {}
  };

  return (
   <header
      className={`backdrop-blur-lg bg-black bg-opacity-70 text-white py-6 md:py-5 px-6 md:px-0 fixed w-full z-40 top-0 left-0 transition-transform duration-300 ease-in-out ${
        showHeader ? "translate-y-0" : "-translate-y-full"
      } rounded-bl-[0px] rounded-br-[0px]`}
    >
      <div className="container flex justify-between items-center">

        {/* Mobile Menu Button */}
        <button
          onClick={() => setIsMobileOpen((v) => !v)}
          className={`md:hidden z-[60] ${isMobileOpen ? "text-black" : "text-white"}`}
          aria-label="Toggle Menu"
        >
          <svg className="h-6 w-6" viewBox="0 0 24 24" aria-hidden="true">
            {isMobileOpen ? (
              <path
                className="fill-current"
                fillRule="evenodd"
                clipRule="evenodd"
                d="M18.3 5.71a1 1 0 011.4 1.42L12.42 14l7.3 7.29a1 1 0 01-1.42 1.4l-7.29-7.3-7.3 7.3a1 1 0 11-1.42-1.42l7.3-7.29-7.3-7.3A1 1 0 113.58 5.7l7.3 7.29 7.29-7.3z"
              />
            ) : (
              <path
                className="fill-current"
                fillRule="evenodd"
                clipRule="evenodd"
                d="M4 5h16a1 1 0 110 2H4a1 1 0 110-2zm0 6h16a1 1 0 110 2H4a1 1 0 110-2zm0 6h16a1 1 0 110 2H4a1 1 0 110-2z"
              />
            )}
          </svg>
        </button>

        {/* Centered Logo (unchanged) */}
        <div className="absolute left-1/2 transform -translate-x-1/2">
          <Link to="/fib" aria-label="Go to FIB Home">
            <img
              src="/Icons and Logos/Logo.png"
              alt="Watches House Logo"
              className="h-12"
            />
          </Link>
        </div>

        {/* Mobile inline greeting (unchanged) */}
        {isAuthenticated && !isMobileOpen && (
          <div className="md:hidden absolute right-4 top-1/2 -translate-y-1/2 z-40 pointer-events-none">
            <span className="block max-w-[9rem] truncate text-right text-xs font-medium text-white/80">
              Hi, <span className="text-white">{user?.displayName || user?.name || "User"}</span>
            </span>
          </div>
        )}

        {/* Mobile Menu Overlay */}
        {isMobileOpen && (
          <div className="absolute inset-0 bg-white text-black bg-opacity-70 backdrop-blur z-40 flex flex-col items-center justify-center min-h-screen text-2xl transition-transform duration-300 ease-in-out">
            {/* Greeting (unchanged) */}
            {isAuthenticated && (
              <div className="absolute top-6 right-6 text-sm text-black/70">
                Hi, <strong className="text-black">{user?.displayName || user?.name || "User"}</strong>
              </div>
            )}

            <nav className="flex flex-col items-center justify-center">
              <Link
                to="/fib"
                className="block px-3 py-3 rounded hover:font-bold"
                onClick={() => setIsMobileOpen(false)}
              >
                Home
              </Link>
              <Link
                to="/fib/store"
                className="block px-3 py-3 rounded hover:font-bold"
                onClick={() => setIsMobileOpen(false)}
              >
                Store
              </Link>
              <Link to="/fib/about" className="block px-3 py-3 rounded hover:font-bold
              onClick={() => setIsMobileOpen(false)}">
                About
              </Link>
              <Link to="/fib/contact" className="block px-3 py-3 rounded hover:font-bold
              onClick={() => setIsMobileOpen(false)}">
                Contact
              </Link>
            </nav>

            {/* Auth actions (mobile) — glass pill over blur */}
            {isAuthenticated ? (
              <button
                onClick={() => {
                  setIsMobileOpen(false);
                  handleLogout();
                }}
                className="mt-8 text-base px-5 py-2.5 rounded-full border border-black/15 bg-white/70 text-black/80 shadow-sm backdrop-blur-md hover:bg-white/85 active:bg-white/90"
                aria-label="Logout"
              >
                Logout
              </button>
            ) : null}
          </div>
        )}

        {/* Desktop spacer/menu/right-side unchanged except for minor hover color alignment */}
        <div className="hidden md:flex md:w-40" />
        <nav className="hidden md:flex md:mx-14 space-x-8">
          <Link to="/fib" className="py-2 rounded hover:font-bold">
            Home
          </Link>
          <Link to="/fib/store" className="py-2 rounded hover:font-bold">
            Store
          </Link>
          <Link to="/fib/about" className="py-2 rounded hover:font-bold">
            About
          </Link>
          <Link to="/fib/contact" className="py-2 rounded hover:font-bold">
            Contact
          </Link>
        </nav>

        <div className="hidden md:flex items-center gap-3">
          {isAuthenticated ? (
            <>
              <span className="text-sm text-white/80">
                Hi, <strong className="text-white">{user?.displayName || user?.name || "User"}</strong>
              </span>
              <button
                onClick={handleLogout}
                className="text-sm text-white/80 px-3 py-1 rounded-full border border-white/30 hover:bg-white/10"
                aria-label="Logout"
              >
                Logout
              </button>
            </>
          ) : (
            <span className="text-sm text-white/60"></span>
          )}
        </div>
      </div>
    </header>

  );
}

export default HeaderFIB;
