// services/fibSsoClient.js
const INIT_URL  = "https://us-central1-watchstore-a1b22.cloudfunctions.net/ssoInitiate";
const VERIFY_URL = "https://us-central1-watchstore-a1b22.cloudfunctions.net/ssoVerify";

export async function initiateSSO() {
  const resp = await fetch(INIT_URL, { method: "POST", headers: { "Content-Type": "application/json" }, body: "{}" });
  const text = await resp.text();
  if (!resp.ok) throw new Error(`initiate failed ${resp.status}: ${text || "<empty>"}`);
  const json = text ? JSON.parse(text) : {};
  if (!json.code) throw new Error("initiate missing 'code' in response");
  return json; // { code, readableId?, lookupKey?, expiresIn? }
}

// Single verifier that forwards payload as-is (expects { code })
export async function verifySSO(payload) {
  const r = await fetch(VERIFY_URL, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify(payload),
  });
  const txt = await r.text().catch(() => "");
  let data = {};
  try { data = txt ? JSON.parse(txt) : {}; } catch {}
  return {
    ok: r.ok,
    status: r.status,
    data,                                 // expect { user?, token?, code?, message? }
    code: data?.code ?? null,
    message: data?.message ?? txt ?? "",
  };
}
