// App.js

import React, { useEffect } from 'react';
import { HashRouter as Router, Routes, Route } from 'react-router-dom';
import Header from './components/Header';
import Footer from './components/Footer';
import HomePage from './pages/HomePage';
import StorePage from './pages/StorePage';
import MiniAppLayout from './layouts/MiniAppLayout.js';
import ProductPage from './pages/ProductPage';
import AboutPage from './pages/AboutPage';
import ContactPage from './pages/ContactPage';
import { initImageCache } from './services/imageService';
import { ProductProvider } from "./contexts/ProductContext";
import QRAccessPage from './pages/QRPage'; // Import the new QR Access page
import StoreLayout from './layouts/StoreLayout';
import { SSOUserProvider } from "./contexts/SSOUserProvider";
import { StoreProvider } from './contexts/StoreContext.js';
import { useLocation, useNavigate } from "react-router-dom";
import { SessionProvider } from "./contexts/SessionProvider";
import HeaderFIB from "./components/HeaderFIB";


function useAutoFibLanding() {
  const loc = useLocation();
  const nav = useNavigate();

  useEffect(() => {
    // With HashRouter, loc.pathname is already "/fib", so no need to read loc.hash
    const b = window?.FIBNativeBridge;
    const isBridge = !!(b && (typeof b.isFIBBridgeAvailable === "function"
      ? b.isFIBBridgeAvailable()
      : b.isFIBBridgeAvailable));
    if (!isBridge) return;

    const atFib = (loc.pathname || "").startsWith("/fib");
    if (!atFib) {
      nav("/fib", { replace: true });
    }
  }, [loc, nav]);
}

// 🔹 New: runs the hook under Router so it has context
function FibGuards({ children }) {
  useAutoFibLanding();
  return children;
}
// function App() {
//   useEffect(() => {
//     initImageCache();
//   }, []);
function AppShell() {
  const location = useLocation();
  const isFib = location.pathname.startsWith("/fib");
  return (
    <>
    {!isFib && <Header />}
        <Routes>
          <Route path="/" element={<HomePage />} />
          <Route element={<StoreLayout />}>
            <Route path="/store" element={<StorePage />} />
            <Route path="/product/:productId" element={<ProductPage />} />
          </Route>
          <Route path="/about" element={<AboutPage />} />
          <Route path="/contact" element={<ContactPage />} />
          <Route path="/access01" element={<QRAccessPage />} />
          <Route
            path="/fib/*"
            element={
              <StoreProvider>
              <SessionProvider>
                <>
                  <HeaderFIB />
                  <MiniAppLayout />
                </>
              </SessionProvider>
              </StoreProvider>
            
            }
            >
            {/* index = /#/fib */}
            <Route index element={<HomePage />} />

             <Route element={<StoreLayout />}>
              <Route path="store" element={<StorePage />} />
              <Route path="product/:productId" element={<ProductPage />} />
              <Route path="about" element={<AboutPage />} />
              <Route path="contact" element={<ContactPage />} />
            </Route>

            

            {/* If you want product details under FIB later:
                <Route path="product/:id" element={<ProductPage />} />
            */}
          </Route>
        </Routes>
        {!isFib && <Footer />}
      </>
  );
}

export default function App() {
  return (
    <ProductProvider>
      <SSOUserProvider>
        <Router>
          <FibGuards><AppShell /></FibGuards>
        </Router>
      </SSOUserProvider>
    </ProductProvider>
  );
}

