import React, { useEffect, useRef, useState } from "react";
import { Outlet, useNavigate, useLocation } from "react-router-dom";
import { useSession } from "../contexts/SessionProvider";
// Service layer must expose these:
// - initiateSSO(): POST to your /ssoInitiate → { code, readableId?, lookupKey?, expiresIn? }
// - verifySSO(payload): POST to your /ssoVerify; payload MUST include { code }
import { initiateSSO, verifySSO } from "../services/fibSsoClient";
import { supabase } from "../supabaseClient";
import { logger } from "../utils/logger";

/** ---------------------------------- Splash (exact spec) ---------------------------------- */
function normalizeFIBUser(apiUser = {}) {
  const {
    name, phoneNumber, dateOfBirth, city, governorate, nationality, iban,
    id, uid, email
  } = apiUser;
  return {
    id: id || uid || `fib:${phoneNumber || iban || name || "unknown"}`,
    displayName: name || phoneNumber || "User",
    name: name || null,
    phone: phoneNumber || null,
    dateOfBirth: dateOfBirth || null,
    city: city || null,
    governorate: governorate || null,
    nationality: nationality || null,
    iban: iban || null,
    email: email || null,
    _raw: apiUser,
  };
}

// Enhanced storeUserData function with extensive logging
async function storeUserData(supabase, userData) {
  const logPrefix = '[storeUserData]';
  
  try {
    // await logger.info(`${logPrefix} `, { userData });

    // Set phone number in logger for tracking
    // if (userData.phoneNumber) {
    //   logger.setPhoneNumber(userData.phoneNumber);
    // }

    // Extract data from the FIB SSO response
    const { name, dateOfBirth, phoneNumber, gender } = userData;
    
    // await logger.debug(`${logPrefix} Extracted fields`, { 
    //   name, dateOfBirth, phoneNumber, gender 
    // });

    // Prepare the user data for database insertion
    const userRecord = {
      name: name || null,
      date_of_birth: dateOfBirth || null,
      phone_number: phoneNumber || null,
      gender: gender || null,
      updated_at: new Date().toISOString()
    };

    // await logger.info(`${logPrefix} `, { userRecord });

    // Test Supabase connection first
    const { data: testData, error: testError } = await supabase
      .from('debug_logs')
      .select('id')
      .limit(1);
    
    if (testError) {
      await logger.error(`${logPrefix} Supabase connection test failed`, { testError });
    } else {
      await logger.debug(`${logPrefix} Supabase connection test passed`);
    }

    // Check if table exists and has correct structure
    const { data: tableInfo, error: tableError } = await supabase
      .from('fibUsers')
      .select('*')
      .limit(0);

    if (tableError) {
      // await logger.error(`${logPrefix} `, { tableError });
      return { success: false, error: tableError };
    } else {
      // await logger.debug(`${logPrefix} `);
    }

    // Try inserting the user data
    // await logger.info(`${logPrefix}`);
    
    const { data, error } = await supabase
      .from('fibUsers')
      .insert(userRecord)
      .select();

    if (error) {
      // await logger.error(`${logPrefix}`, { 
      //   error, 
      //   userRecord,
      //   errorCode: error.code,
      //   errorMessage: error.message,
      //   errorDetails: error.details,
      //   errorHint: error.hint
      // });
      return { success: false, error };
    }

    // await logger.info(`${logPrefix}`, { data });
    return { success: true, data };

  } catch (err) {
    // await logger.error(`${logPrefix} Unexpected error`, { 
    //   error: err.message,
    //   stack: err.stack,
    //   userData 
    // });
    return { success: false, error: err };
  }
}

// Also update the storeUserInDatabase function
async function storeUserInDatabase(supabase, rawUser) {
  // await logger.info('[storeUserInDatabase] Called with rawUser', { rawUser });
  
  const userData = {
    name: rawUser.name,
    dateOfBirth: rawUser.dateOfBirth,
    phoneNumber: rawUser.phoneNumber,
    gender: rawUser.gender
  };

  // await logger.debug('[storeUserInDatabase] Prepared userData', { userData });
  
  const result = await storeUserData(supabase, userData);
  
  // await logger.info('[storeUserInDatabase] Result', { result });
  
  return result;
}


export function removeSplash(el, durationMs = 1000) {
  if (!el) return Promise.resolve();
  el.style.setProperty("--fib-splash-duration", `${durationMs}ms`);

  return new Promise((resolve) => {
    try {
      el.classList.remove("enter");
      el.classList.add("leave");

      const onEnd = (e) => {
        if (e.target !== el || e.propertyName !== "opacity") return;
        el.removeEventListener("transitionend", onEnd);
        try { document.getElementById("fib-splash-style")?.remove(); } catch {}
        try { el.remove(); } catch {}
        resolve();
      };
      el.addEventListener("transitionend", onEnd, { once: true });

      // Fallback: a bit over the CSS duration
      const safety = Math.max(durationMs + 200, 800);
      setTimeout(() => {
        if (document.body.contains(el)) {
          try { document.getElementById("fib-splash-style")?.remove(); } catch {}
          try { el.remove(); } catch {}
        }
        resolve();
      }, safety);
    } catch {
      try { el.remove(); } catch {}
      resolve();
    }
  });
}


function setImgHints(img) {
  // Hint the browser to decode ASAP; helps with slow-first-paint logos
  img.decoding = "async";
  try { img.fetchPriority = "high"; } catch {}
  img.loading = "eager";
}
// --- splash ---
export function createSplash() {
  // prevent duplicates
  const existing = document.getElementById("fib-splash");
  if (existing) return existing;

  // style (adds enter/leave + staggered child fades)
  let style = document.getElementById("fib-splash-style");
  if (!style) {
    style = document.createElement("style");
    style.id = "fib-splash-style";
    style.textContent = `
      @keyframes fadein { from { opacity: 0 } to { opacity: 1 } }
      @keyframes pulse   { 0%,100%{opacity:.4} 50%{opacity:1} }

      #fib-splash {
        --fib-splash-duration: 1500ms; 
        opacity: 0;
        transition: opacity var(--fib-splash-duration) ease;
      }
      #fib-splash.enter { opacity: 1; }
      #fib-splash.leave { opacity: 0; }
      
      /* Logo fade-in control - start hidden, fade in when ready */
      #fib-splash .brand { 
        opacity: 0; 
        transform: translateY(6px);
        transition: opacity calc(var(--fib-splash-duration) * 0.8) ease, 
                   transform calc(var(--fib-splash-duration) * 0.8) ease;
        filter: drop-shadow(0 2px 10px rgba(0,0,0,.35));
      }
      #fib-splash.ready .brand { 
        opacity: 1; 
        transform: translateY(0);
      }

      #fib-splash .fade  { animation: fadein .5s ease-out both }
      #fib-splash .pulse { animation: pulse 1.4s ease-in-out infinite }
      
      #fib-splash .vline { 
        width: 2px; 
        background: rgba(255, 255, 255, 0.6);
        height: 60px;
        margin: 0;
        display: block;
      }

      #fib-splash .dot { 
        width: 15px;
        height: 15px;
        background: rgba(255, 255, 255, 0.6);
        border-radius: 50%; 
        margin: 0;
        display: block;
      }

      #fib-splash .arrow {
        margin: 0;
        display: block;
        height: 12px;
      }

      #fib-splash .arrow::before {
        content: '';
        display: block;
        width: 24px;
        height: 12px;
        border-bottom: 2px solid rgba(255, 255, 255, 0.6);
        border-left: 2px solid rgba(255, 255, 255, 0.6);
        border-right: 2px solid rgba(255, 255, 255, 0.6);
        border-top: none;
        border-radius: 0 0 24px 24px;
        margin: 0 auto;
      }

      #fib-splash .caption { color: #d8d8d8; font-size: 14px; margin: 8px 0; }

      /* Stagger children reveal */
      #fib-splash .stagger > * {
        opacity: 0; 
        transform: translateY(6px);
        transition: opacity calc(var(--fib-splash-duration) * 0.8) ease, 
                   transform calc(var(--fib-splash-duration) * 0.8) ease;
      }
      #fib-splash.ready .stagger > * { opacity: 1; transform: translateY(0); }
      
      /* Stagger timing - logos get their own delays */
      #fib-splash.ready .brand:nth-of-type(1) { transition-delay: .05s; } /* Top logo */
      #fib-splash.ready .stagger > *:nth-child(1){ transition-delay: .15s; } /* Dot */
      #fib-splash.ready .stagger > *:nth-child(2){ transition-delay: .25s; } /* Line 1 */
      #fib-splash.ready .stagger > *:nth-child(3){ transition-delay: .35s; } /* Text */
      #fib-splash.ready .stagger > *:nth-child(4){ transition-delay: .45s; } /* Line 2 */
      #fib-splash.ready .stagger > *:nth-child(5){ transition-delay: .55s; } /* Arrow */
      #fib-splash.ready .brand:nth-of-type(2) { transition-delay: .65s; } /* Bottom logo */
    `;
    document.head.appendChild(style);
  }

  // root
  const el = document.createElement("div");
  el.id = "fib-splash";
  Object.assign(el.style, {
    position: "fixed",
    inset: "0",
    background: "#0d0f12", color: "#f7f7f7",
    display: "flex", flexDirection: "column",
    alignItems: "center", justifyContent: "center",
    textAlign: "center", zIndex: 99999,
    fontFamily: "system-ui, -apple-system, Segoe UI, Roboto, Arial",
  });

  // top logo
  const top = document.createElement("img");
  top.src = "/Icons%20and%20Logos/fibLogo.png";
  top.alt = "First Iraqi Bank";
  setImgHints(top);
  Object.assign(top.style, { width: "140px", marginBottom: "16px" });
  top.className = "brand";
  // Remove the immediate show on load - let CSS animations handle it
  el.appendChild(top);

  // mid stack
  const mid = document.createElement("div");
  mid.className = "stagger";
  Object.assign(mid.style, { 
    display: "flex", 
    flexDirection: "column", 
    alignItems: "center", 
    margin: "8px 0"
  });

  ["dot pulse","vline fade","caption fade","vline fade","arrow pulse"].forEach(cls => {
    const n = document.createElement("div");
    n.className = cls;
    if (cls.startsWith("caption")) {
      n.textContent = "On your way to...";
    }
    mid.appendChild(n);
  });

  el.appendChild(mid);

  // bottom logo
  const bottom = document.createElement("img");
  bottom.src = "/Icons%20and%20Logos/Logo.png";
  bottom.alt = "Watch House";
  setImgHints(bottom);
  Object.assign(bottom.style, { width: "120px", marginTop: "16px" });
  bottom.className = "brand";
  // Remove the immediate show on load - let CSS animations handle it
  el.appendChild(bottom);

  // mount + entry
  document.body.appendChild(el);
  
  // Force layout, then enter
  requestAnimationFrame(() => {
    el.classList.add("enter");
    // Mark ready so children/brands stagger in after the root fade
    requestAnimationFrame(() => el.classList.add("ready"));
  });

  return el;
}



/** ---------------------------------- Helpers ---------------------------------- */
const SPLASH_MIN_MS = 400;
const MAX_SSO_ATTEMPTS_PER_DAY = 5;

function fromQS(name) { try { return new URLSearchParams(window.location.search).get(name); } catch { return null; } }
function devBypassEnabled() { return localStorage.getItem("FIB_DEV_BYPASS") === "1" || fromQS("fib_dev") === "1"; }
function setDevBypass() {
  if (fromQS("fib_dev") === "1") {
    localStorage.setItem("FIB_DEV_BYPASS", "1");
    localStorage.setItem("FIB_DEV_NAME", fromQS("name") || "Dev User");
  }
}
function devName() { return localStorage.getItem("FIB_DEV_NAME") || "Dev User"; }
function dashless(v) { return String(v || "").replaceAll("-", ""); }
function getBridge() {
  return (typeof window !== "undefined" && window.FIBNativeBridge && window.FIBNativeBridge.isFIBBridgeAvailable)
    ? window.FIBNativeBridge
    : null;
}

/** ---------------------------------- Component ---------------------------------- */
export default function MiniAppLayout() {
  const { isAuthenticated, expired, setSession, canAttemptSSO, recordSSOAttempt } = useSession();
  const [blocked, setBlocked] = useState(false);
  const [error, setError] = useState("");

  const splashElRef = useRef(null);
  const splashT0 = useRef(Date.now());
  const cancelRef = useRef(false);
  const pollTimerRef = useRef(null);

  const nav = useNavigate();
  const loc = useLocation();

  // Splash mount/unmount
  useEffect(() => {
    splashElRef.current = createSplash();
    setDevBypass();
    return () => {
      cancelRef.current = true;
      if (pollTimerRef.current) clearInterval(pollTimerRef.current);
      removeSplash(splashElRef.current);
      const style = document.getElementById("fib-splash-style");
      if (style && style.parentNode) style.parentNode.removeChild(style);
    };
  }, []);

  async function finishSplash() {
    const dt = Date.now() - splashT0.current;
    const wait = Math.max(0, SPLASH_MIN_MS - dt);
    if (wait) await new Promise(r => setTimeout(r, wait));
    removeSplash(splashElRef.current);
  }

 useEffect(() => {
  let cancel = false;
  let pollTimer = null;
  let pollingStarted = false;
  

  const finishSplashSafe = async () => { try { await finishSplash(); } catch {} };
  const stopPolling = async (msg) => {
    if (pollTimer) { clearInterval(pollTimer); pollTimer = null; }
    if (msg) setError(msg);
    pollingStarted = false;
    await finishSplashSafe();
  };

  const startVerifyPollingByCode = (code, { intervalMs = 2000, maxMs = 120000 } = {}) => {
    if (pollingStarted || !code) return;         // guard double start
    pollingStarted = true;

    const t0 = Date.now();
    let inFlight = false;

    pollTimer = setInterval(async () => {
      if (cancel || inFlight) return;
      if (Date.now() - t0 > maxMs) return stopPolling("Authorization timed out.");

      inFlight = true;
      try {
        await new Promise(resolve => setTimeout(resolve, 2000));
        const res = await verifySSO({ code });   // non-throwing
        if (!res.ok) {
          // Don't terminate on 401, wait 2 seconds and continue polling
          if (res.status === 401) {
            await new Promise(r => setTimeout(r, 2000));
            return; // continue polling
          }
          if ([403,410,429].includes(res.status)) return stopPolling(res.message || `Access denied (${res.status}).`);
          if (res.status >= 500) await new Promise(r => setTimeout(r, 1500));
          return; // keep polling for 400
        }
        const rawUser = res.data;
        const safeToken = res.data.token?.value ? res.data.token : null; 

        if (rawUser) {
          clearInterval(pollTimer); pollTimer = null; pollingStarted = false;
          const user = normalizeFIBUser(rawUser);
          await storeUserInDatabase(supabase, rawUser);
          setSession({ user, token: safeToken });
          if (!loc.pathname.startsWith("/fib")) nav("/fib", { replace: true });
          await finishSplashSafe();
        }
      } finally { inFlight = false; }
    }, intervalMs);
  };

  (async () => {
    // 0) Reuse existing app session
    if (isAuthenticated && !expired) {
      // Add 2-second delay for existing sessions before removing splash
      await new Promise(resolve => setTimeout(resolve, 2000));
      await finishSplashSafe();
      return;                                    // IMPORTANT
    }

    // 1) Dev bypass
    if (devBypassEnabled()) {
      const exp = Math.floor(Date.now()/1000) + 2*60*60;
      setSession({ user: { id:"dev-uid", displayName: devName(), email:"dev@example.com" }, token: { value:"dev-token", exp } });
      if (!loc.pathname.startsWith("/fib")) nav("/fib", { replace:true });
      await finishSplashSafe();
      return;
    }

    // 2) Must be inside the bank app
    const bridge = getBridge();
    if (!bridge) {
      setError("Please open this mini-app from inside the bank app.");
      await finishSplashSafe();
      return;
    }

    // 3) Throttle attempts
    if (!canAttemptSSO()) {
      setError(`Daily SSO attempts exceeded (> ${MAX_SSO_ATTEMPTS_PER_DAY}).`);
      setBlocked(true);
      await finishSplashSafe();
      return;
    }
    

    // 4) INITIATE → CODE
    const state = { code: null, ridDashless: null };

    // Modify the success handling in your existing code
const onAuthOk = async () => {
  await new Promise(resolve => setTimeout(resolve, 2000));
  let res = await verifySSO({ code: state.code });
  
  if (!res.ok && res.status === 401) {
    await new Promise(resolve => setTimeout(resolve, 2000));
    res = await verifySSO({ code: state.code });
  }
  
  if (!res.ok) {
    if ([401,403,410,429].includes(res.status)) 
      return stopPolling(res.message || `Access denied (${res.status}).`);
    return;
  }
  
  const { user: rawUser, token } = res.data || {};
  if (!rawUser) return;
  
  // Store user data in database
  await storeUserInDatabase(supabase, rawUser);
  
  const user = normalizeFIBUser(rawUser);
  const safeToken = token?.value ? token : null;
  setSession({ user, token: safeToken });
  
  if (!loc.pathname.startsWith("/fib")) nav("/fib", { replace: true });
  await finishSplashSafe();
};

    const onAuthFail = async () => stopPolling("Native authentication failed.");

    bridge.addEventListener?.("AUTHENTICATED", onAuthOk);
    bridge.addEventListener?.("AUTHENTICATION_FAILED", onAuthFail);

    try {
      const init = await initiateSSO();   
      recordSSOAttempt();              // { code, readableId? }
      state.code = init.code;
      if (!state.code) throw new Error("Missing code from initiate");
      state.ridDashless = String(state.code).replace(/-/g, "");

      bridge.sendMessage?.({ type: "AUTHENTICATE", body: { readableId: state.ridDashless } });

      // Start fallback polling once (after 10s) — single source of truth
      setTimeout(() => {
        if (!cancel) startVerifyPollingByCode(state.code, { intervalMs: 2000, maxMs: 120000 });
      }, 10000);
    } catch (e) {
      setError(`Access issue • ${e?.message || e}`);
      await finishSplashSafe();
    }
  })();

  return () => {
    cancel = true;
    if (pollTimer) clearInterval(pollTimer);
    const bridge = getBridge();
    bridge?.removeEventListener?.("AUTHENTICATED");
    bridge?.removeEventListener?.("AUTHENTICATION_FAILED");
  };
// eslint-disable-next-line react-hooks/exhaustive-deps
}, []);




  // Error / blocked screen (splash already removed)
  const showIssue = blocked || error;
  if (showIssue) {
    return (
      <div className="min-h-screen flex items-center justify-center p-6">
        <div className="max-w-md w-full border rounded-2xl p-6">
          <h1 className="text-xl font-semibold mb-2">Access issue</h1>
          <p className="text-sm text-gray-600">{error || "SSO unavailable or attempts exceeded."}</p>
        </div>
      </div>
    );
  }

  return <div className="min-h-screen"><Outlet /></div>;
}
