import React, { createContext, useContext, useEffect, useMemo, useState } from "react";

/** Storage + limits **/
const STORAGE_KEY = "SSO_SESSION_V1";
const MAX_SSO_ATTEMPTS_PER_DAY = 5; // bank example cap

function todayKey() {
  const d = new Date();
  const y = d.getFullYear();
  const m = String(d.getMonth() + 1).padStart(2, "0");
  const day = String(d.getDate()).padStart(2, "0");
  return `${y}${m}${day}`;
}

function readSession() {
  try {
    const raw = localStorage.getItem(STORAGE_KEY);
    if (!raw) return null;
    const data = JSON.parse(raw);
    // If token has exp (seconds since epoch), enforce expiry
    if (data?.token?.exp && Date.now() >= data.token.exp * 1000) {
      // expired -> treat as no session
      return { ...data, expired: true };
    }
    return data;
  } catch {
    return null;
  }
}

function writeSession(session) {
  localStorage.setItem(STORAGE_KEY, JSON.stringify(session));
}

function clearSession() {
  localStorage.removeItem(STORAGE_KEY);
}

function getAttempts() {
  try {
    const raw = localStorage.getItem(STORAGE_KEY);
    if (!raw) return 0;
    const data = JSON.parse(raw);
    const key = todayKey();
    return data?.sso?.attempts?.[key] ?? 0;
  } catch {
    return 0;
  }
}

function bumpAttempt() {
  const raw = localStorage.getItem(STORAGE_KEY);
  let data = {};
  try {
    data = raw ? JSON.parse(raw) : {};
  } catch {}
  const key = todayKey();
  data.sso = data.sso || {};
  data.sso.attempts = data.sso.attempts || {};
  data.sso.attempts[key] = (data.sso.attempts[key] ?? 0) + 1;
  localStorage.setItem(STORAGE_KEY, JSON.stringify(data));
}

const SessionCtx = createContext({
  user: null,
  token: null,                // { value, exp?: seconds }
  isAuthenticated: false,
  expired: false,
  attemptsToday: 0,
  canAttemptSSO: () => true,
  recordSSOAttempt: () => {},
  setSession: (_session) => {},
  login: (_session) => {},
  logout: () => {},
});

export function SessionProvider({ children }) {
  const [session, setSession] = useState(() => readSession());

  useEffect(() => {
    if (session) writeSession(session);
  }, [session]);

  const value = useMemo(() => {
    const user = session?.user ?? null;
    const token = session?.token ?? null;
    const expired = !!session?.expired;
    const attemptsToday = getAttempts();
    return {
      user,
      token,
      isAuthenticated: !!user && !expired,
      expired,
      attemptsToday,
      canAttemptSSO: () => attemptsToday < MAX_SSO_ATTEMPTS_PER_DAY,
      recordSSOAttempt: () => bumpAttempt(),
      setSession: (next) => setSession(next),
      login: (next) => setSession(next),
      logout: () => {
        clearSession();
        setSession(null);
      },
    };
  }, [session]);

  return <SessionCtx.Provider value={value}>{children}</SessionCtx.Provider>;
}

export function useSession() {
  return useContext(SessionCtx);
}
